body {
  font-family: 'Comic Sans MS', 'Comic Sans';
  margin: 0;
  background: #F8C728;
  background-image: linear-gradient(#FB722D .3em, transparent .3em), linear-gradient(90deg,  #FB722D .3em, transparent .3em);
  background-size: 5em 5em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Comic Sans MS', 'Comic Sans';
  text-align: center;
}

#header {
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 100000ms;
          animation-duration: 100000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

#header div {
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 100000ms;
          animation-duration: 100000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

#header div ul li {
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 100000ms;
          animation-duration: 100000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

button {
  font-family: 'Comic Sans MS', 'Comic Sans';
}

.spinny-time {
  -webkit-animation-name: spin-reverse;
          animation-name: spin-reverse;
  -webkit-animation-duration: 100000ms;
          animation-duration: 100000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes spin {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(360deg);
              transform:rotate(360deg);
  }
}

@keyframes spin {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(360deg);
              transform:rotate(360deg);
  }
}
@-webkit-keyframes spin-reverse {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(-360deg);
              transform:rotate(-360deg);
  }
}
@keyframes spin-reverse {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(-360deg);
              transform:rotate(-360deg);
  }
}



#header {
    display: flex;
    justify-content: space-between;
    color: #6A392E;
    border-bottom: .3em solid #FB722D;
}

#header div {
    width: 50%;
    background-color: #FFFAFA;
}

#header .directory-list {
    display: flex;
    list-style: none;

    justify-content: right;
}

#header .directory-list li {
    margin: 3%;

}

#header .directory-list li a {
    text-decoration: none;
    color: #6A392E;
}

#header .directory-list li a:hover {
    text-decoration: underline;
}

